* {
  box-sizing: border-box;
}

body {
  --background-color: white;
  --text-color: black;
  --elements-color: white;
  margin: 0;
  font-family: Nunito, sans-serif;
}

main, header {
  background-color: var(--background-color);
  color: var(--text-color);
}

main.dark, header.dark {
  --background-color: #202c37;
  --text-color: white;
  --elements-color: #2b3945;
}

main {
  min-height: calc(100vh - 73px);
  padding: 24px;
}

a {
  color: inherit;
  text-decoration-line: none;
}

.header-container {
  background-color: var(--elements-color);
  z-index: 1;
  padding-inline: 24px;
  position: sticky;
  top: 0;
  box-shadow: 0 2px 4px #0000001a;
}

.header-content {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin-inline: auto;
  display: flex;
}

.title {
  font-weight: 800;
}

.theme-changer {
  cursor: pointer;
}

.countries-container {
  margin-top: 16px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 32px;
  max-width: 1200px;
  margin-inline: auto;
  display: grid;
}

.country-card {
  background-color: var(--elements-color);
  border-radius: 8px;
  padding-bottom: 24px;
  transition: all .2s ease-in-out;
  display: inline-block;
  overflow: hidden;
  box-shadow: 0 0 8px #0003;
}

.flag-container {
  height: 160px;
  overflow: hidden;
}

.country-card img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.country-card:hover {
  transform: scale(1.03);
  box-shadow: 0 0 16px #0003;
}

.card-title {
  margin-block: 16px;
  font-size: 24px;
}

.card-text {
  padding-inline: 16px;
}

.card-text p {
  margin-block: 8px;
}

.search-filter-container {
  margin-inline: auto;
  justify-content: space-between;
  gap: 24px;
  max-width: 1200px;
  margin-top: 16px;
  margin-bottom: 48px;
  display: flex;
}

.search-container {
  color: var(--text-color);
  background-color: var(--elements-color);
  border-radius: 4px;
  width: 100%;
  max-width: 350px;
  padding-left: 24px;
  overflow: hidden;
  box-shadow: 0 0 8px #00000026;
}

.search-container input {
  background-color: inherit;
  width: 90%;
  color: inherit;
  border: none;
  outline: none;
  padding: 16px;
}

.search-container input::placeholder {
  color: #999;
}

.filter-by-region {
  background-color: var(--elements-color);
  width: 250px;
  color: var(--text-color);
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 16px;
  box-shadow: 0 0 8px #00000026;
}

@media (width <= 768px) {
  .countries-container {
    justify-content: center;
  }

  .title {
    font-size: 16px;
  }

  .header-content p {
    font-size: 12px;
  }

  .search-filter-container {
    flex-direction: column;
  }
}

main .shimmer-card {
  --shimmer-color: #c7c7c7;
}

main.dark .shimmer-card {
  --shimmer-color: #445869;
}

.shimmer-card {
  background-color: var(--elements-color);
  height: 338px;
}

.shimmer-card .flag-container {
  background-color: var(--shimmer-color);
}

.shimmer-card .card-title {
  background-color: var(--shimmer-color);
  border-radius: 4px;
  height: 32px;
}

.shimmer-card p {
  background-color: var(--shimmer-color);
  border-radius: 4px;
  height: 22px;
}

.shimmer-card p:last-child {
  width: 60%;
}

.country-details-container {
  margin-inline: auto;
  max-width: 1200px;
  margin-top: 32px;
}

.back-button {
  cursor: pointer;
  background-color: var(--elements-color);
  border-radius: 4px;
  padding: 6px 24px;
  box-shadow: 0 0 4px #0003;
}

.country-details {
  align-items: center;
  gap: 64px;
  margin-top: 64px;
  display: flex;
}

.country-details img {
  border: 1px solid #ddd;
  width: 40%;
}

.details-text {
  flex-flow: column wrap;
  gap: 12px 64px;
  max-height: 180px;
  display: flex;
}

.details-text p {
  margin: 0;
}

.border-countries {
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 8px;
  margin-top: 48px;
  display: flex;
}

.border-countries a {
  background-color: var(--elements-color);
  border-radius: 4px;
  margin-top: 10px;
  padding: 4px 16px;
  font-size: 14px;
  display: inline-block;
  box-shadow: 0 0 4px #0003;
}

a:not(:first-child) {
  margin-left: 10px;
}

@media (width <= 940px) {
  .country-details {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    margin-top: 32px;
  }

  .details-text {
    max-height: unset;
  }

  .country-details img {
    width: 100%;
    max-width: 400px;
  }
}

.shimmer .flag {
  background-color: var(--elements-color);
  border-radius: 4px;
  width: 40%;
  height: 240px;
}

.shimmer .title {
  background-color: var(--elements-color);
  border-radius: 4px;
  height: 36px;
}

.country-details.shimmer p {
  background-color: var(--elements-color);
  border-radius: 2px;
  height: 22px;
}

.country-details.shimmer p:last-child {
  width: 50%;
}

.details-text-container {
  flex-grow: 1;
}

@media (width <= 940px) {
  .shimmer .flag, .details-text-container {
    width: 100%;
    max-width: 400px;
  }
}
